<template>
	<v-card outlined class="ma-1 pa-1">
			<div :class="`text-${message.author.id != $store.state.userId ? 'right': 'left'} font-weight-bold`"> {{ message.author.username }} </div>
			<div :class="`text-${message.author.id != $store.state.userId ? 'right': 'left'} text-caption text--disabled`">{{message.createdAt | dateFrTchat}}</div>

			<div :class="`text-${message.author.id != $store.state.userId ? 'right': 'left'}`" v-html="markDownText"></div>
	</v-card>
</template>
<script>
import marked from "marked";
marked.setOptions({ gfm: true, breaks: true });

export default {
	props: {
		message: {},
	},
	data() {
		return {
			refresh: false,
		};
	},
 computed: {
    markDownText() {
      let result = "", underliner = this.message.content.split("__");

      while (underliner.length) {
        if (underliner.length == 1) result += underliner.shift();
        else if (underliner.length % 2) result += underliner.shift() + "<u>";
        else result += underliner.shift() + "</u>";
      }
      return marked(result == "" ? this.message.content : result);
    },
  },
};
</script>
