<template>
	<div class="TchatMenu d-flex flex-column justify-end align-center">
		<!-- Au dessus sse trouve tout els lobbys auquel l'utilisateur est connecté -->
		<div class="d-flex flex-column-reverse justify-center align-center" v-if="openTchat">
			<div class="tchatActionsButtons d-flex justify-center">
				<!-- Bouton plus pour creer un lobby de tchat  -->
				<div class="d-flex align-center justify-center">
					<v-text-field
						class="outsideInput ma-2"
						solo
						placeholder="Créer lobby"
						v-if="addLobby"
						v-model="newLobbyName"
						@keyup.enter="newLobbyName ? connectLobby() : (addLobby = !addLobby)"
					/>

					<v-btn
						class="accent ma-2"
						:small="!addLobby"
						fab
						v-show="!lookingFor"
						@click="
							newLobbyName
								? connectLobby()
								: ((addLobby = !addLobby),$store.commit('setActiveTchatLobby', undefined))
						"
					>
						<v-icon>{{ !lookingFor ? "mdi-plus" : "mdi-chevron-right" }}</v-icon>
					</v-btn>
				</div>
				<!-- Bouton magnify pour rechercher un lobby de tchat public  -->
				<div class="d-flex align-center justify-center">
					<v-text-field
						class="outsideInput ma-2"
						solo
						placeholder="rechercher"
						v-if="lookingFor"
						v-model="searchLobby"
						@keyup.enter="lookingFor = !lookingFor"
					/>

					<v-btn
						class="accent ma-2"
						:small="!lookingFor"
						fab
						v-show="!addLobby"
						@click="
							lookingFor = !lookingFor;
							$store.commit('setActiveTchatLobby', undefined);
						"
					>
						<v-icon>mdi-magnify</v-icon>
					</v-btn>
				</div>
			</div>
			<TchatLobby
				v-for="lobby in $store.state.dataBase.lobbys"
				:key="lobby.id"
				:id="lobby.id"
			/>
		</div>

		<v-btn
			class="secondary ma-2"
			fab
			@click="
				openTchat = !openTchat;
				$emit('tchatOpen', openTchat);
			"
		>
			<v-icon>mdi-message</v-icon>
		</v-btn>
	</div>
</template>

<script>
import Vue from "vue";
import TchatLobby from "./TchatLobby.vue";

export default {
	updated() {
		var x = document.getElementsByClassName("tchatMessages");
		if (!x.length) return;
		x = x[0];
		x.scrollTo(0, x.scrollHeight + 2000);
	},
	components: {
		TchatLobby,
	},
	data() {
		return {
			openTchat: true,
			// openTchat: false,
			lookingFor: false,
			addLobby: false,

			searchLobby: "",
			newLobbyName: "",

			refresh: false,
			tchatList: [],
			// tchatlobbysId:[],

			friendsList: [],

			openContent: false,
		};
	},
	methods: {
		connectLobby() {
			this.$webSocket.connectLobby(this.newLobbyName);
			this.$store.commit("setActiveTchatLobby", this.newLobbyName);
			this.$store.commit("refreshActiveTchatMessages", this.newLobbyName);
			this.newLobbyName = "";
			this.addLobby = false;
		},
	},

	created() {
		Vue.prototype.$tchat = this;
	},
};
</script>

<style>
.TchatMenu {
	position: relative;
	right: 0;
	bottom: 0;
	width: 6em;
}
.tchatActionsButtons {
	height: 4em;
	width: 6em;
}
.outsideInput {
	position: absolute;
	right: 6em;
	bottom: 2.6em;
	width: 18em;
}
</style>
