<template>
  <div class="NotifArea d-flex flex-column-reverse" app>
      <Notifications
        v-for="(element) in notifications"
        :title="element.title"
        dense
        :message="element.message"
        :timer="element.timer"
        :key="element.order"
        :type="element.type"
        :slot="element.slot"
      />
  </div>
</template>
<script>
import Notifications from "./Notifications.vue";

export default {
  components:{Notifications},
  
created(){
      this.$app.notif = this.notif;
},

  data() {
    return {
      notifications: [],

    };
  },
  methods:{
    /**
     * @param {Object} event
     * * title : Titre de la notif
     * * message : contenue de la notif (invisible si pas déployer par l'utilisateur)
     * * type : type de notification ("sucess", "warning", "error", "info")
     * * timer : combien de seconde doit apparaitre la notification
     * * slot : Element html a intégrer dans le corps de la notif
     */
    notif(event) {
      if (typeof event == "string")
        event = { title: event, type: "info", timer: 5 };
      event.order = this.notifications.length + 1;
      this.notifications.push(event);
    },
  }
};
</script>
<style>
/* N'apparait pas au premier plan */
.NotifArea {
  position: fixed;
  top: 3.6em;
  right: 0em;
  margin-right: auto;
  margin-left: auto;
  min-width: 12em;
  max-width: 20em;
  z-index: 1000;
}

</style>

