<template>
	<div class="Tchat d-flex">
		<!-- <v-expand-x-transition > -->
		<TchatContent
			v-if="$store.state.activeTchatLobbyId && openTchat && !addLobby && !lookingFor"
			:id="$store.state.activeTchatLobbyId"
		/>
		<!-- </v-expand-x-transition> -->
		<TchatMenu @tchatOpen="tchatOpen"/>

	</div>
</template>

<script>
import Vue from "vue";
import TchatMenu from "./TchatMenu.vue";
import TchatContent from "./TchatContent.vue";

export default {
	updated() {
		var x = document.getElementsByClassName("tchatMessages");
		if (!x.length) return;
		x = x[0];
		x.scrollTo(0, x.scrollHeight + 2000);
	},
	components: {
		TchatMenu,
		TchatContent,
	},
	data() {
		return {
			openTchat: true,
			// openTchat: false,
			lookingFor: false,
			addLobby: false,

			searchLobby: "",
			newLobbyName: "",

			refresh: false,
			tchatList: [],
			// tchatlobbysId:[],

			friendsList: [],

			openContent: false,
		};
	},
	methods: {
		connectLobby() {
			this.$webSocket.connectLobby(this.newLobbyName);
			this.$store.commit("setActiveTchatLobby", this.newLobbyName);
			this.$store.commit("refreshActiveTchatMessages", this.newLobbyName);
			this.newLobbyName = "";
			this.addLobby = false;
		},
		tchatOpen(value) {
			this.openTchat = value;
		},
	},

	created() {
		Vue.prototype.$tchat = this;
	},
};
</script>

<style>
.Tchat {
	position: fixed;
	right: 1em;
	bottom: 2em;
}
</style>
