<template>
	<div class="page">
		<Lamp
			v-for="objet in connectedObjects"
			:key="objet.id"
			:id="objet.id"
			:topic="objet.topic"
			:type="objet.type"
			:actions="objet.actions"
			class="primary ma-1"
		></Lamp>
		{{ $store.getters.getAll("awaitResponses") }}
	</div>
</template>

<script>
import Lamp from "../components/Domotique/Lamp.vue";
export default {
	name: "Home",
	url: "/",
	components: {
		Lamp,
	},
	computed: {
		topic() {
			return this.$store.state.topics;
		},

		connectedObjects() {
			return this.$store.getters.getAll("connectedObjects");
		},
	},
};
</script>

