import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);

const dataBase = {
	// namespaced: true,
	state: {
		// BASE DE DONNEES
		users: [],
		lobbys: [],
		messages: [],
		games: [],
		topics: [],
		connectedObjects: [],
		awaitResponses: [],
	},
	getters: {
		index: (state) => (type, id) => {
			for (let i in state[type]) if (state[type][i].id == id) return i;
			return undefined;
			// return state[type].indexOf((val)=>val.id == id)
		},
		filter: (state) => (type, pattern) => {
			return state[type].filter((val) => val.id.includes(pattern));
		},
		get: (state) => (type, id) => {
			return state[type].find((val) => val.id == id);
		},

		getAll: (state) => (type) => {
			return state[type];
		},

		getConnectedObject: (state) => (id) => {
			let connectedObject = state.connectedObjects.find((val) => val.id.includes(id));
			if (!connectedObject) return undefined;

			connectedObject.topics = [];
			for (let topic of state.topics) {
				if (topic.id.includes(id)) {
					connectedObject.topics.push(topic);
				}
			}
			return connectedObject;
		},
	},

	mutations: {
		setBD(state, { type, id, value }) {
			// console.log("setBD "+ type, state[type])
			let actualIndex = this.getters.index(type, id);
			if (!actualIndex) state[type].push(value);
			else state[type].splice(actualIndex, 1, value);
		},

		updateBD(state, { type, id, value }) {
			let updateValue = (value, modif) => {
				let result = JSON.parse(JSON.stringify(value));
				for (let i in modif) result[i] = modif[i];
				return result;
			};

			let actualIndex = this.getters.index(type, id);
			let actualValue = state[type][actualIndex];
			state[type].splice(actualIndex, 1, updateValue(actualValue, value)); //TODO a tester
		},

		delete(state, {type, id}) {
			// console.log("delete", actualIndex, state[type], state[type][actualIndex] );
			let actualIndex = this.getters.index(type, id);
			if (!actualIndex) return;
			state[type].splice(actualIndex, 1);
		},
	},
	actions: {},
};

/**
 * Donnée en provenance du broker
 */
const topics = {
	state: { cache: [] },

	mutations: {
		setTopic(state, data) {
			let { topic, value } = data;
			state.cache.push({ topic, value });
			console.log("setTopic", data);
		},
	},
	getters: {
		getTopicList: (state) => () => {
			return state.cache;
		},
	},
};

const store = new Vuex.Store({
	modules: {
		dataBase,
		topics,
	},
	state: {
		//Données systeme générale
		connected: false,
		error: false,

		//Données de l'utilisateur
		userId: undefined,
		userUserName: undefined,
		userToken: undefined,

		//Données lié au Tchat
		tchatlobbysId: [],
		activeTchatLobbyId: undefined,
		activeTchatMessages: [],

		// BASE DE DONNEES
		users: [],
		lobbys: [],
		messages: [],
		games: [],

		//Donnée lié au jeu
		activeGameId: undefined,
		activeGameData: {
			players: ["user1", "user2"],
			board: [
				[1, 2],
				[2, 1],
				[1, 2],
				[2, 1],
				[1, 2],
				[2, 1],
				[1, 2],
			],
		},
	},
	mutations: {
		// setBD(state,{ type, id, value}){
		//     let actual = getValueIndex(state[type], id)
		// 	if (!actual)state[type].push(value)
		//     else state[type].splice(actual[1], 1, value) //TODO a tester
		// },
		// updateBD(state, {type, id, value}){
		//     let actual = getValueIndex(state[type], id)
		//     state[type].splice(actual[1], 1, updateValue(actual[0], value)) //TODO a tester
		// },

		// delete(state, type, id){
		//     state[type].splice(Array.indexOf(state[type][id]), 1)
		// } ,

		connexion(state, bool) {
			state.connected = bool;
		},
		error(state, bool) {
			state.error = bool;
		},

		setUser(state, data) {
			let { id, token, username } = data;
			if (id) state.userId = id;
			if (username) state.userUserName = username;
			if (token) state.userToken = token;
		},

		//TCHAT
		setActiveTchatLobby(state, lobbyId) {
			state.activeTchatLobbyId = lobbyId;
		},
		refreshActiveTchatMessages(state) {
			if (Vue.prototype.$dataBase.lobbys.has(state.activeTchatLobbyId))
				state.activeTchatMessages = Vue.prototype.$dataBase.lobbys.get(
					state.activeTchatLobbyId
				).messages;
			else state.activeTchatMessages = [];
		},

		//GAME
		setActiveGameId(state, gameid) {
			state.activeGameId = gameid;
		},

		updateActiveGameData(state, data) {
			// result = state.activeGameData
			console.log("updateActiveGameData", data);

			for (let i in data) {
				state.activeGameData[i] = data[i];
			}
		},
	},
});

export default store;
