import Vue from 'vue'

class Log{
    constructor(log, index, logs){
        let { timestamp, event, dataUpdate } = log
        if (!event || !dataUpdate) throw new Error('Une log doit tjrs avoir event ou dataUpdate')
        this.timestamp = timestamp ? timestamp : Date.now()
        this.event = event
        this.data = dataUpdate

        // a declarer avec Object defined propoerty
        this.getLogs = ()=>logs
        this.getIndexInLogs = ()=>index

    }

    /**
     * Renvoie la log precedent celle ci ou undefined si c'est la première
     * @returns {Log}
     */
    prev(){
        if (this.getIndexInLogs()) return undefined
        return this.getLogs()[this.getIndexInLogs()-1]

    }
    /**
     * Renvoie la log suivant celle ci ou undefined si c'est la dernière
     * @returns {Log}
     */
     next(){
        if (this.getIndexInLogs() == this.getLogs().length ) return undefined
        return this.getLogs()[this.getIndexInLogs()+1]
    }
}


class Logs extends Array{
    /**
     * Log d'un jeu, tjrs par ordre croissant de timestamps
     * @param {Array} logs Logs bruts
     */
    constructor(logs){
        super()
        for (let i in logs)this.addLog(logs[i])
    }

    checkTimestamps(){

    }
    addLog(log){
        return this.push(new Log(log, this.length, this))
    }
}


class Game {
    constructor( data={} ){
        let { id, lobbyId, type, owner, playerSizeLimit, playersId, state, createdAt, start, end, logs } = data
        this.id = lobbyId +'--'+id
        this.lobbyId = lobbyId
        this.type = type
        this.owner = owner
        this.playerSizeLimit = playerSizeLimit
        this.playersId = playersId ?playersId : []
        this.state = state ? state : 'create'

        this.createdAt = createdAt ? createdAt : Date.now()
        this.start = start
        this.end = end
        this.logs = new Logs(logs)    
    }

    /**
     * Retirer un joueur de la liste de joueur
     * @param {String} playerId identifiant de User correspondant au joueur
     */
     addPlayer(playerId){
        if (this.playersId.length >= this.playerSizeLimit) throw new Error('La partie est pleine')
        if (this.state !== "create") throw new Error('La partie est finit')
        this.playersId.push(playerId)
    }

    /**
     * Retirer un joueur de la liste de joueur
     * @param {String} playerId identifiant de User correspondant au joueur
     */
    removePlayer(playerId){
        if (this.state == "finish") throw new Error('La partie est finit')
        this.playersId.splice(Array.indexOf(this.playersId[playerId]), 1)
    }

    /**
     * Déclarer le démarrage du jeu
     */
    starting(){
        this.state = "inProgress"
    }

    /**
     * Déclarer la fin du jeu
     */
    ending(){
        this.state = "finish"
    }

    /**
     * Recuperer les infos du jeux
     * @returns 
     */
    getInfos(){
        return JSON.parse(JSON.stringify(this))
    }

    update(data){
        for (let i in data){
            this[i] = data[i]
        }
        Vue.prototype.$store.commit('updateActiveGameData', data)

    }

    clean(replace = {}){
        for (let i in this)delete this[i]
        
        for (let i in replace) this[i] = replace[i]
        

    }
}


export default Game