<template>
	<div class="d-flex flex-column align-start justify-start">

		<TopicsListing :topics="$store.getters.getAll('topics')" class="ma-4" />
	</div>
</template>

<script>
import TopicsListing from "../components/Domotique/TopicsListing.vue";

export default {
	name: "Domotique",
	icon: "home",
	data() {
		return {
			topic: undefined, //STRING
			value: undefined, //STRING

			id: undefined,
			methode: undefined,
			args: undefined,
		};
	},
	components: {
		TopicsListing,
	},

	methods: {
		publishTopic(topic, value) {
			console.log();
			this.$webSocket.publishTopic(topic, value);
		},
		connectedObjectAction(id, methode, args) {
			if (args) args = args.split(", ");
			this.$webSocket.connectedObjectAction(id, methode, args);
		},
	},
};
</script>
