<template>
	<div 
		:fab="$store.state.activeTchatLobbyId != id"
		:class="'tchatLobby ma-1 pa-1 d-flex align-center justify-center ' + ($store.state.activeTchatLobbyId == id ? 'open' : '')"
		:disabled="!$store.getters.get('lobbys', id)"
		@click="setActiveTchatLobby()"
		>{{ id }}
		<!-- <v-avatar>
				<v-img class="ma-2" :src="lobby.avatarUrl" />
			</v-avatar> -->
	</div>
</template>
<script>
export default {
	props: {
		id: {
			required: true,
			type: String,
		},
		// refreshed:{
		//     type:Number,
		//     default:()=> Date.now()
		// }
	},

	data() {
		return {
			active: false,
			refresh: false,
		};
	},
	computed: {
		lobby() {
			return this.$dataBase.lobbys.get(this.id);
		},
	},
	methods: {
		setActiveTchatLobby() {
			if (this.$store.state.activeTchatLobbyId == this.id)
				return this.$store.commit("setActiveTchatLobby", undefined);
			this.$store.commit("setActiveTchatLobby", this.id);
			this.$store.commit("refreshActiveTchatMessages");
			this.$webSocket.connectLobby(this.id);

			this.$tchat.addLobby = undefined;
			this.$tchat.lookingFor = undefined;
		},
	},
};
</script>

<style scoped>
.tchatLobby{
	width: 90%;
	height: 4em;
	text-align: center;
	overflow: hidden;
	background-color: var(--v-primary-base);
		border-radius: 12px;
	box-shadow: 1px 2px 3px 2px rgba(0, 0, 0, 0.315);
	transition-duration: 0.4;
}
.tchatLobby:hover{
	box-shadow: 2px 4px 12px 2px rgb(0, 0, 0);
	transform: translate(-1px, -1px);

}
.open {
	background-color: var(--v-primary-lighten2);
	font-weight: bold;
	box-shadow: 2px 4px 12px 2px rgba(0, 0, 0, 0.719);
}
</style>