<template>
	<v-menu bottom left offset-y :close-on-content-click="false">
		<template v-slot:activator="{ on, attrs }">
			<!-- <v-text-field prepend-inner-icon="mdi-account-group" dense hide-details v-model="lobbyId"></v-text-field> -->
			<v-btn
				class="d-flex justify-center"
				v-bind="attrs"
				width="13em"
				height="50px"
				tile
				v-on="on"
			>
				<v-icon class="mr-2" :color="$store.state.error ? 'error' : ''">{{
					$store.state.error ? "mdi-alert" : "mdi-account"
				}}</v-icon>
				{{ $store.state.userUserName ? $store.state.userUserName : "Login" }}
				<!-- <v-icon v-if="error" color="error">mdi-alert</v-icon> -->
			</v-btn>
		</template>

		<v-card class="userPanel pa-4 d-flex justify-center">
			<div class="d-flex flex-column">
				<div class="d-flex">
				<v-text-field
					prepend-inner-icon="mdi-account" hide-details=""
					v-model="username"
					@keyup.enter="updateUser"
					@input="usernameChange = !usernameChange"
				/>
				<v-icon v-if="$store.state.userUserName != username" class="ml-2" @click="updateUser">mdi-content-save</v-icon></div>
				<v-switch hide-details v-model="autoName" label="autoName"></v-switch>
				<v-switch v-model="autoConnect" label="autoConnect"></v-switch>

				<v-btn @click="clickLogin" class="primary">Se connecter</v-btn>
			</div>
		</v-card>
	</v-menu>
</template>

<script>
import WebSocketClient from "../../mechanics/WebSocketClient.js";
const domain = window.location.hostname; // TODO get domain from config

export default {
	data() {
		return {
			usernameChange:true
		};
	},

	computed: {
		username: {
			get() {
				this.usernameChange
				let result = localStorage.getItem("username");
				return result;
			},
			set(value) {
				localStorage.setItem("username", value);
			},
		},
		autoName: {
			get() {
				let result = localStorage.getItem("autoName");
				return result;
			},
			set(value) {
				localStorage.setItem("autoName", value);
			},
		},
		autoConnect: {
			get() {
				let result = localStorage.getItem("autoConnect");
				if (result) new WebSocketClient(this.$app.$store, domain);
				return result;
			},
			set(value) {
				localStorage.setItem("autoConnect", value);
			},
		},
	},
	methods: {
		updateUser() {
			this.$webSocket.updateUser({ username: this.username });
		},
		clickLogin() {
			if (!this.$webSocket) new WebSocketClient(this.$app.$store, domain);
		},
	},
};
</script>

<style scoped>
.userPanel {
	width: 16em;
}


</style>
