class User {
	/**
	 *
	 * @param {Number} data.id Numero identifiant
	 * @param {String} data.token Token permettant de s'authentifier depuis le client
	 * @param {String} data.username Nom définit par l'utilisateur ou User X
	 * @param {Boolean} data.visible Utilisateur visible par tous
	 * @param {Number} data.createdAt Timestamps de creation
	 */
	constructor(data) {
		let { id, token, username, visible, createdAt } = data
		this.id = id;
		this.token = token;
		this.username = username;
		this.visible = visible
		this.createdAt = createdAt
		this.lobbys = []; // Gestionnaire de salon où se trouve l'utilisateur
	}

	isMe(){}
	
	updateObject(data){
		for (let i in data)this[i] = data[i]
		if (this.component){this.component.refresh = !this.component.refresh}
	}

}

module.exports = User
