<template>
	<div>
		<v-switch
			v-if="action.type == 'onOff'"
			readonly
			color="secondary"
			@click="clickConnectedObjectAction(actionMethod)"
			:input-value="action.value"
			inset
			:label="action.name"
			:error-messages="error"
			:loading="loading"
		>
		</v-switch>
		<div v-else-if="action.type == 'gradient'">
			<!-- <v-progress-linear class="slider" color="grey"  :value="action.value"></v-progress-linear> -->
			<v-slider
				class="slider"
				color="secondary"
				:max="action.valueMax"
				:min="action.valueMin"
				v-model="target"
				:value="action.value"
				inverse-label
				:label="action.name"
				:error-messages="error"
				:loading="loading"
				thumb-label="always"
				:thumb-size="18"
				@change="changeConnectedObjectAction(actionMethod, !action.value)"
			></v-slider>
		</div>

		<v-btn v-else>Inconnu</v-btn>
	</div>
</template>

<script>
export default {
	props: {
		id: {
			type: String,
			required: true,
		},
		type: {
			type: String,
			required: true,
		},
		name: {
			type: String,
			required: true,
		},
		connectedObject: {
			type: Object,
			required: true,
		},
		timer: {
			type: Number,
			required: false,
			default() {
				return 2500;
			},
		},
	},

	data() {
		return {
			error: "", //mettre un niveau de couleur en fonction du timer
			target: "",
		};
	},
	computed: {
		actionMethod() {
			switch (this.action.type) {
				case "onOff":
					if (this.action.value) return "setOff"
					else return "setOn"
				case "gradient":
					return "setGradient";
			}
			return this.action.type;
		},
		action() {
			let actions = this.connectedObject.actions;

			for (let i in actions) {
				let action = actions[i];
				if (action.type !== this.type || action.name !== this.name) continue;
				// if(action.type !== "gradient") console.log(this.connectedObject.actions);

				let topic = "";
				if (action.type == "onOff")
					topic = `${this.connectedObject.topic}/${this.id}/${action.response.topicToggle}`;
				else if (action.type == "gradient") {
					topic = `${this.connectedObject.topic}/${this.id}/${action.response.topicGradient}`;
				}

				action.topic = this.$store.getters.get("topics", topic.replace(/\..+/, ""));
				action.value = this[`${action.type}ValueGetter`](action, topic);
				// console.log(action.type, topic,action.topic, action.value);

				return action;
			}
			return undefined;
		},
		loading() {
			return this.$store.getters.get(
				"awaitResponses",
				`${this.id}-${this.actionMethod}-${this.name}`
			)
				? true
				: false;
		},
	},

	methods: {
		onOffValueGetter(action, topic) {
			if (!action.topic) return;
			let value;
			if (topic.includes("."))
				value = JSON.parse(action.topic.value)[topic.replace(/.+\./, "")];
			else value = action.topic.value;
			if (value == action.response.valueOn) return true;
			if (value == action.response.valueOff) return false;
		},

		gradientValueGetter(action, topic) {
			if (!action.topic) return;
			let value;
			if (topic.includes("."))
				value = JSON.parse(action.topic.value)[topic.replace(/.+\./, "")];
			else value = action.topic.value;
			return value;
		},

		changeConnectedObjectAction(methode) {
			this.connectedObjectAction(methode, this.name, [this.target]);
		},

		clickConnectedObjectAction(methode) {
			this.connectedObjectAction(methode, this.name);
		},

		connectedObjectAction(action, actionID, args) {
			// console.log("connectedObjectAction", (methode, args));
			this.$webSocket.connectedObjectAction(
				this.id,
				this.connectedObject.type,
				action,
				actionID,
				args
			);
		},
	},
};
</script>

<style>
.slider {
	width: 200px !important;
}
</style>
