import Vue from "vue";
import User from "./User.js";
import Lobby from "./Lobby.js";
import Message from "./Message.js";
import Game from "./games/Game.js";


class StoreMap {
	constructor (store, type){
		this.store = store
		this.type = type
	}
	get(id){
		return this.store.getters.get(this.type, id)
	}
	getAll(){
		return this.store.state.dataBase[this.type]
	}

	has(id){
		if (this.store.getters.get(this.type, id)) return true
		return false
	}

	set(id, value){
        this.store.commit('setBD', {type:this.type, id, value})
		return id, value
	}
	update(id, value){
		this.store.commit('updateBD', {type:this.type, id, value})
	
	}
}

class DataBase {
	constructor(store, webSocket) {
		this.store = store
		this.webSocket = webSocket

		this.users = new StoreMap(store, 'users');
		this.lobbys = new StoreMap(store, 'lobbys');
		this.messages = new StoreMap(store, 'messages');
		this.games = new StoreMap(store, 'games');

		Vue.prototype.$dataBase = this;
	}

	fetchAll() {
		return new Promise((res) => {
			this.fetchUsers();
			this.fetchLobbys();
			this.fetchMessages()

			res();
		});
	}

	fetchUser(id) {
		this.webSocket.socket.emit("Data", `users/${id}`);
	}
	fetchUsers() {
		this.webSocket.socket.emit("Data", "users");
	}
	setUser(data) {
		this.users.set(data.id, new User(data));
	}
	setUsers(users) {
		for (let i in users) this.setUser(users[i]);
	}

	fetchLobby(id) {
		this.webSocket.socket.emit("Data", `lobbys/${id}`);
	}
	fetchLobbys() {
		this.webSocket.socket.emit("Data", "lobbys");
	}
	setLobby(data) {
		this.lobbys.set(data.id, new Lobby(data));
		this.setMessages(data.messages)
	}
	

	setLobbys(lobbys) {
		for (let i in lobbys)this.setLobby(lobbys[i]);
	}

	fetchMessage(id) {
		this.webSocket.socket.emit("Data", `messages/${id}`);
	}
	fetchMessages() {
		this.webSocket.socket.emit("Data", "messages");
	}
	setMessage(data) {
		let message = new Message(data)
		let lobby = this.lobbys.get(data.lobbyId)
		this.messages.set(data.id, message);
		lobby.messages.push(message)
	}
	setMessages(messages) {
		for (let i in messages) this.setMessage(messages[i]);
	}


	fetchGame(id) {
		this.webSocket.socket.emit("Data", `games/${id}`);
	}
	fetchGames() {
		this.webSocket.socket.emit("Data", "games");
	}
	setGame(data) {
		let game = new Game(data)
		let lobby = this.lobbys.get(data.lobbyId)
		this.games.set(data.id, game);
		lobby.games.push(game)
	}
	setGames(games) {
		for (let i in games) this.setGame(games[i]);
	}
}

export default DataBase;
