import Vue from "vue";
class Api{
    constructor(/*name*/){
        // $api[name] = this
    }
    get(value, timeout=1000){
        return new Promise((sucess)=>{
            setTimeout(()=>{
                sucess(value)
            },timeout)
        })        
    }
    // post(){

    // }
    // update(){}
    // delete(){}
}

Vue.prototype.$api = new Api()

export default Api