// import Vue from 'vue'

class Lobby {
	constructor(data) {
		let { id, owner, token, sizeLimit, name, visible, createdAt , users} = data
		this.id = id;
		this.owner = owner;
		this.token = token;
		this.sizeLimit = sizeLimit;
		this.name = name;
		this.visible = visible
		this.createdAt = createdAt

		this.users = users ? users : [];
		this.usersTyping = []; // liste les id des utilisateurs entrain de taper un message
		this.messages = [];
		this.games = []
	}
	
	updateObject(data){
		for (let i in data)this[i] = data[i]
	}
}

export default Lobby