import Vue from 'vue'
const weekDayNames = ['dimanche', 'lundi', 'mardi', 'mercredi', 'jeudi', 'vendredi', 'samedi']
const monthNames = ['janvier', 'février', 'mars', 'avril', 'mai', 'juin', 'juillet', 'août', 'septembre', 'octobre', 'novembre', 'décembre']

Vue.filter('dateFrTchat' , function(value){
	if(!value) return '';
	var date = new Date(value);
	let now = new Date()
	let result
	//Adapter en fonction de l'ancienneté de la date
	if (date.getFullYear() !== now.getFullYear()) result = `${date.getDate()} ${monthNames[date.getMonth()]} ${date.getFullYear()}`
	else {
		if (date.getMonth() !== now.getMonth()) result = `${date.getDate()} ${monthNames[date.getMonth()]}`
		else {
			if(now.getDate()-date.getDate() == 2) result = `avant-hier, à ${date.getHours()}h${date.getMinutes() <10 ? '0'+date.getMinutes() : date.getMinutes()}`
			else if(now.getDate()-date.getDate() == 1) result = `hier, à ${date.getHours()}h${date.getMinutes() <10 ? '0'+date.getMinutes() : date.getMinutes()}`
			else if(now.getDate() == date.getDate()) result =  `aujourd'hui, à ${date.getHours()}h${date.getMinutes() <10 ? '0'+date.getMinutes() : date.getMinutes()}`
			else result = `${weekDayNames[date.getDay()]} ${date.getDate()} ${monthNames[date.getMonth()]}`
		}
	}
	return result
    // return date.toLocaleString("fr-FR").split(' ')[0].replace(',', ' ');
})