
class Message {
	constructor(data) {
		let { content, author, lobbyId, id, createdAt } = data
		this.content = content;
		this.author = author;
		this.lobbyId = lobbyId;
		this.id = id
		this.createdAt = createdAt

		this.distributed = [];
		this.viewed = [];

		this.component
	}

	updateObject(data){
		for (let i in data) this[i] = data[i]
		if(this.component) this.component.refresh = !this.component.refresh
	}

}

module.exports = Message