<template>
	<v-card class="TchatContent  ma-2 d-flex flex-column">
		<div class="primary">
			<v-card-title>{{ id }}</v-card-title>
			<!-- <v-card-subtitle>{{`${users.length} utilisateur${users.length > 1 ? "s": ""}`}}</v-card-subtitle> -->
		</div>
		<v-divider color="accent"></v-divider>
		<div class="tchatMessages scrollBar d-flex flex-column pa-1 ">
			<TchatMessage
				v-for="message in activeTchatMessages"
				:key="message.id"
				:message="message"
			/>

			<v-textarea
				class="messageTypingArea"
				solo
				outlined
				rows="1"
				auto-grow
				color="accent"
				append-icon="mdi-message"
				v-model="newMessage"
				@focus="setScroll"
				@click:append="send"
				@keyup.enter="send"
			/>
		</div>
	</v-card>
</template>
<script>
import TchatMessage from "./TchatMessage.vue";

export default {
	components: { TchatMessage },
	props: {
		id: {
			required: true,
			type: String,
		},
	},

	data() {
		return {
			newMessage: "",
			refresh: false,
		};
	},
	methods: {
		send() {
			this.$webSocket.sendMessage(this.$store.state.activeTchatLobbyId, this.newMessage);
			this.newMessage = undefined;
			var x = document.getElementsByClassName("tchatMessages")[0];
			console.log((x.scrollTop = x.scrollHeight));
			// messageBody.scrollBottom = 99999999999999 // messageBody.scrollHeight - messageBody.clientHeight;
		},
		setScroll() {
			var x = document.getElementsByClassName("tchatMessages")[0];
			x.scrollTo(0, x.scrollHeight + 200);
		},
	},
	computed: {
		lobby() {
			return this.$dataBase.lobbys.get(this.id);
		},
		activeTchatMessages() {
			return this.$store.state.activeTchatMessages;
		},
	},
	watch: {
		activeTchatMessages() {
			this.setScroll();
		},
		newMessage() {
			this.setScroll();
		},
	},
};
</script>
<style lang="scss">
.TchatContent {
	width: 16em;
	position: relative;
	max-height: 80vh;
}
.tchatMessage {
	scroll-behavior: auto;
}
</style>
