<template>
  <div :class="classBox" v-if="$app.screen > hide">
    <v-btn
      :class="classBtn"
      v-for="item in navMenu"
      :key="item.id"
      :active-class="activeClass"
      height="50"
      depressed
      text
      tile
      dense
      :to="item.url"
    >
      <v-icon v-show="$app.screen > hide" class="mx-1">{{ item.icon }}</v-icon>
      <v-expand-x-transition>
        <div v-show="$app.screen > smaller">{{ item.name.toUpperCase() }}</div>
      </v-expand-x-transition>
    </v-btn>
  </div>
</template>
<script>
export default {
  props: {
    buttons: {
      type: Array,
    },
    smaller: {
      default: 0,
      type: Number,
    },
    hide: {
      default: 0,
      type: Number,
    },
    classBox: {
      default: "d-flex ma-auto",
      type: String,
    },
    classBtn: {
      default: "secondary ma-1 d-flex",
      type: String,
    },
    activeClass: {
      type: String,
    },
  },

  computed: {
    navMenu() {
      let navMenu = [];

      if (this.buttons) {
        return this.buttons;
      } else {
        for (let i in this.$pages) {
          if (this.$pages[i].navMenu) navMenu.push(this.$pages[i]);
        }
      }
      return navMenu;
    },
  },
};
</script>
