<template>
  <v-app id="app" v-resize="onResize">
    <v-main>
      <v-container
        class="d-flex align-start fill-height"
        :justify-center="screen < 750"
        fluid
      >

        <router-view></router-view>
      </v-container>
    </v-main>

    <NavDrawer />
    <AppBar />
    <NotificationsArea />
    <Tchat/>
    <ScrollUpButton />
    <Footer />
  </v-app>
</template>

<script>
import Vue from "vue";

import NavDrawer from "./components/App/NavDrawer.vue";
import AppBar from "./components/App/AppBar.vue";
import NotificationsArea from "./components/App/NotificationsArea.vue";
import Footer from "./components/App/Footer.vue";
import ScrollUpButton from "./components/App/ScrollUpButton.vue";

import Tchat from "./components/Tchat/Tchat.vue";

export default {
  name: "App",

  components: {
    AppBar,
    NotificationsArea,
    Footer,
    NavDrawer,
    ScrollUpButton,
    Tchat
  },

  created() {
    Vue.prototype.$app = this;
  },

  data() {
    return {
      navDrawer: false,
      screen: 900,
      url: window.location.origin,
      userIsConnect: false,
    };
  },

  methods: {
    onResize() {
      this.screen = window.innerWidth;
    }
  },
};
</script>
<style>
@import './style.scss';
/* @import './styleVuetify.scss'; */

#app > div > main {
  background-color: var(--v-primary-darken4);
  background-size: cover;
  transition: 1s;
}

.container {
  padding: 0px;
}

.page {
  padding: 12px;
  width: 100%;
  height: 100%;
    background-size: cover;

  animation:.6s ease-out fadeInOpacity;
}


@keyframes fadeInOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
</style>
