<template>
	<div>
		<div>
			<v-text-field
				class="searchSizer mr-4"
				v-model="filter"
				label="Rechercher"
				clearable
			></v-text-field>
			<v-chip-group column class="d-flex align-center flex-wrap">
				<v-chip
					v-for="topic in topicsList.sort()"
					:key="topic"
					@click="clickOnly(topic)"
					:class="only.includes(topic) ? 'primary' : 'secondary'"
				>
					{{ topic }}
				</v-chip>
			</v-chip-group>
			<v-chip-group column class="d-flex align-center flex-wrap">
				<v-chip v-if="only.length" @click="clickResetFilter()" class="error">
					<v-icon>mdi-delete</v-icon>
				</v-chip>
				<v-chip
					v-for="topic in only"
					:key="topic"
					@click="clickOnly(topic)"
					:class="only.includes(topic) ? 'primary' : 'secondary'"
				>
					{{ topic }}
				</v-chip>
			</v-chip-group>
		</div>

		<div>
			<v-data-table :headers="headers" :items="filteredData" @click:row="(item)=>editableTopic = editableTopic == item.id?'':item.id">
				<template v-slot:item.id="{ item }">
					<v-chip-group>
						<span v-for="(topic, index) in item.id.split('/')" :key="topic">
							<v-chip
								small
								@click="clickOnlyInTopic(topic, item.id.split('/'))"
								:class="only.includes(topic) ? 'primary' : 'secondary'"
							>
								{{ topic }}
							</v-chip>
							<span class="mr-2" v-if="index + 1 < item.id.split('/').length"> / </span>
						</span>
					</v-chip-group>
				</template>
				<template v-slot:item.value="{ item }">
					<div class="d-flex align-center">
					<div v-if="editableTopic != item.id" class="tableValue" v-html="renderFilterHtml(item.value, filter)"></div>
					<v-text-field v-else @click.stop :v-model="item.value" class="tableValue" append-icon="mdi-send" @click:append="$app.$webSocket.publishTopic(item.id, item.value)" >{{item.actions}} </v-text-field>
					<!-- <v-btn @click="editableTopic = editableTopic == item.id?'':item.id" icon><v-icon color="grey">mdi-pen</v-icon></v-btn> -->
					</div>
				</template>

			</v-data-table>
		</div>
	</div>
</template>
<script>
export default {
	props:["topics"],
	data() {
		return {
			filter: "",
			only: [],
			editableTopic: "",
		};
	},
	computed: {
		headers() {
			return [
				{
					text: "Topic",
					value: "id",
				},
				{
					text: "Value",
					value: "value",
				},
			];
		},

		topicsList() {
			let topics = [];
			if (this.only.length)
				topics = this.topics.filter((topic) => {
					return (
						topic.id.split("/")[this.only.length - 1] == this.only[this.only.length - 1]
					);
				});
			else topics = this.topics;

			let results = [];
			for (let i in topics) {
				let topic = topics[i].id.split("/")[this.only.length];
				if (results.indexOf(topic) == -1 && topic) results.push(topic);
			}

			return results;
		},

		filteredData() {
			let topics = this.topics;
			let results = [];
			let isFiltered = (topic) => {
				if (this.filter == "" || !this.filter) return true;
				if (
					/*topic.id.indexOf(this.filter) != -1 ||*/ topic.value
						.toLowerCase()
						.indexOf(this.filter.toLowerCase()) != -1
				)
					return true;
				return false;
			};

			for (let i in topics) {
				if (!isFiltered(topics[i])) continue;
				if (!this.isOnly(topics[i].id)) continue;
				results.push(topics[i]);
			}
			return results;
		},
	},
	methods: {
		isOnly(topicId) {
			let subTopics = topicId.split("/");

			for (let i in this.only) {
				if (!subTopics.includes(this.only[i])) return false;
			}
			return true;
		},
		clickFilter(topic) {
			if (this.filter.includes(topic)) {
				this.filter.splice(this.filter.indexOf(topic), 1);
			} else {
				this.filter.push(topic);
			}
		},
		clickOnly(topic) {
			if (this.only.includes(topic)) {
				this.only.splice(this.only.indexOf(topic), 1);
			} else {
				this.only.push(topic);
			}
		},

		clickOnlyInTopic(topic, topics) {
			let result = [];
			for (let i in topics) {
				let top = topics[i]
				result.push(top);

				if (topic == top ) return (this.only = result);
			}
			// if (this.only.includes(topic)) {
			// 	this.only.splice(this.only.indexOf(topic), 1);
			// } else {
			// 	this.only.push(topic);
			// }
		},
		clickResetFilter() {
			this.only = [];
		},
		renderFilterHtml(text, filter) {
			if (filter == "" || filter == undefined) return text;
			let result =
				"<span>" +
				text.replace(
					filter,
					`</span><span class="primary" >` + filter + "</span><span>"
				) +
				"</span>";
			console.log(result);
			return result;
		},
	},
};
</script>
<style scoped>
.focus {
	text-decoration: underline;
	text-decoration-style: solid;
	/* text-underline-offset: -1px; */
	text-decoration-thickness: 1px;
}



.tableValue {
	max-width: 500px;
	/* min-width: 30vw; */
	white-space: pre-wrap;
	word-wrap: break-word;
}
</style>
