import Vue from "vue";

const pages = {};
const routes = [];

class Page {
	/**
	 * Class dédiée aux pages du site web qui appraissent dna sle routeur et dans la navMenu
	 * @param {String} id identifiant pour retrouver le composant et définir son url
	 * @param {Vue.Component} component composant vueJs qui seras monter si son url est appelé
	 * @param {Boolean} navMenu Est ce qeu cette page pour apparaitre dans le menus de navigation
	 * @param {Object} options les options de parametrage de la page (qui prennent le pas sur ce qui est déclaré dans le composant)
	 * * name : Nom qui servira d'identifiant et de texte pour le bouton de navigation
	 * * icon : Icon qui apparaitra dans le navMenu (ajout automatique de "mdi-")
	 * * url : Url qui permettra de trouver la page
	 */
	constructor(component, navMenu = true, id = undefined, options = {}) {
		if (!component.name && !options.name)
			throw new Error(`Le composant ${component.__file} doit posséder un "name"`);
		if (!component) throw new Error(`Aucun composant valide fournit`);
		if (!id && !component.name) throw new Error(`Aucun identifiant de la page`);


		//Les variables indiquées en option supplante celle fournie par le composant
		if (options.name) component.name = options.name;
		if (options.icon) component.icon = options.icon;
		if (options.url) component.url = options.url;

		this.component = component;
		this.name = component.name;
		this.id = id ? id : component.name;
		this.icon = "mdi-" + (component.icon ? component.icon : component.name.toLowerCase());
		this.url = component.url ? component.url : `/${component.name}`;

		this.navMenu = navMenu;

		if (!this.url) this.url = "/" + this.id.toLowerCase(); //Si pas de "url", indique une url par defaut basé sur le "name"
		if (!this.url.match(/\/.*/)) throw new Error(`L'url fournit est incorrect`);
		pages[this.id] = this;
		routes.push({ path: this.url, component });
	}
}

//Importation des composants de page
import Home from "./Home.vue";
// import Test from "./Test.vue";
// import Socket from "./Socket.vue";
// import Game from "./Game.vue";
import Domotique from "./Domotique.vue";

const pagesToInstanciate = [Home, /*Test, Socket, Game,*/ Domotique];

for (let i in pagesToInstanciate) new Page(pagesToInstanciate[i]);

Vue.prototype.$pages = pages;
export default routes;
